<template>
	<div class="secondpage">
		<div class="title_top">
			<el-row :gutter="20">
				<el-col :span="16" :offset="4">
					<div class="secondpage_con">
						<h2></h2>
						<p></p>
					</div>
				</el-col>
			</el-row>
		</div>
		<!--  -->
		<el-row class="phonebox">
			<el-col :span="16" :offset="4">
				<el-row>
					<el-col :xs="24">
						<h3></h3>
						<p></p>
					</el-col>
					<el-col :xs="12" :sm="6" v-for="phonelist in content" :key="index">
						<img src="../../static/1587_jhh3umir.png" width="30%" alt="">
						<h4>{{phonelist.title}}</h4>
						<p>{{phonelist.phonenum}}</p>
					</el-col>
				</el-row>
			</el-col>
		</el-row>

		<el-row class="formbox">
			<el-col :span="16" :offset="4">
				<el-row>
					<el-col :xs="24" :sm="18" class="formleft_box">
						<h2></h2>
						<el-form :label-position="labelPosition">
							<el-form-item label="" >
								<el-input placeholder=""></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-input placeholder="" ></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-input type="textarea" placeholder=""></el-input>
							</el-form-item>
								<el-button type="primary"></el-button>
						</el-form>
					</el-col>
					<el-col :xs="24" :sm="6" class="formright_box">
						<h2></h2>
						<p></p>
						<el-button type="primary" round></el-button>
					</el-col>
				</el-row>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				labelPosition:"top",
				workTask: [],
				content:[
					{
						id:1,
						imgsrc:'1587_jhh3qqh1.png',
						title:'',
						phonenum:'100-0000-0000'
					},
					{
						id:1,
						imgsrc:'1587_jhh3umir.png',
						title:'',
						phonenum:'1000000@qq.com'
					},
					{
						id:1,
						imgsrc:'1587_jhh3ut0l.png',
						title:'',
						phonenum:'666'
					},
					{
						id:1,
						imgsrc:'1587_jhh3uzi5.png',
						title:'',
						phonenum:' 9:00-7:00'
					}
				]
			};
		},
		created() {
			this.workTask = this.$route.query.id
			console.log(this.workTask)
		}

	}
</script>

<style scoped>
	.title_top {
		width: 100%;
		overflow: hidden;
		margin: 0;
		color: #FFFFFF;
		background: url(../assets/1587_jgzynhtp.jpg) no-repeat center center;
		-webkit-background-size: cover;
		-moz-background-size: cover;
		-o-background-size: cover;
		background-size: cover;
	}

	.secondpage_con {
		margin: 150px auto;
	}
	.phonebox{
		margin: 50px auto;
	}
	.phonebox p,.phonebox h3,.phonebox h4{
		color: #000000;
	}

	.formbox{
		padding-top: 50px;
		padding-bottom: 50px;
		background-color: rgb(248, 248, 249);
	}
	.formleft_box{
		text-align: left;
		color: #000000;
		padding-right:50px ;
	}
	.el-textarea textarea{
		min-height: 150px;
	}
	.formright_box{
		text-align: left;
	}
	.formright_box h2,.formright_box p{
		color: #000000;
	}
	.formright_box p{
		font-size: 14px;
		color: rgb(153, 153, 153);
		margin: 50px auto;
	}

</style>



